        @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
        @import url('https://fonts.googleapis.com/css2?family=Fraunces:ital,opsz,wght@0,9..144,100..900;1,9..144,100..900&display=swap');

        * {
            padding: 0;
            margin: 0;
        }

        body {
            height: 100vh;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: hsl(30, 38%, 92%);
            font-family: 'Montserrat', sans-serif;
        }

        #gremio {
            display: flex;
            height: 400px;
            width: 550px;
            border-radius: 30px;
            background-color: white;
            box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
        }

        #tudo {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 20px;
            flex: 1;
        }

        img {
            border-radius: 10px 0 0 10px;
            width: 275px;
            height: auto;
        }

        #perfume {
            font-size: 12px;
            font-family: 'Fraunces', serif;
        }

        #nome {
            font-size: 28px;
            margin: 5px 0;
        }

        #descricao {
            font-size: 12px;
            margin: 5px 0;
            color: hsl(228, 12%, 48%);
        }

        #preco {
            display: flex;
            align-items: center;
            font-size: 12px;
            color: hsl(158, 36%, 37%);
        }

        .money {
            font-size: 28px;
            margin-right: 10px;
        }

        .ja {
            text-decoration: line-through;
            color: hsl(228, 12%, 48%);
        }

        #cart {
            display: flex;
            align-items: center;
            background-color: hsl(158, 36%, 37%);
            color: white;
            height: 50px;
            width: 100%;
            border-radius: 10px;
            justify-content: center;
            cursor: pointer;
            font-family: 'Montserrat', sans-serif;
            gap: 10px;

            &:hover {
                background-color: hsl(147, 100%, 19%);
            }
        }

        .cart2 {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
        }

        .cart-icon {
            width: 1em;
            height: auto;
        }